.landingPageContainer{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    height: 100vh;
    margin-left: 10%;
    
}
.landingContainer p{
    font-size: 16px;
    font-weight: 300;
    color: #ffffff80; /* Ensure high contrast with background */
}
.bigName{
    font-size: 60px;
    font-weight: 700;
    color: #ffffff; /* Ensure high contrast with background */
    margin-top: 5px;
}
.title{
    font-size: 24px;
    font-weight: 400;
    color: #00ffff; /* Ensure high contrast with background */
    margin-top: 5px;
}
.commentary{
    margin-top: 50px;
}
.commentary p{
    font-size: 14px;
    font-weight: 300;
    color: #a0bfc4; /* Increase contrast */
    margin-top: 15px;
}
.const{
    font-size: 14px;
    font-weight: 400;
    color: #cd5c5c; /* Ensure high contrast with background */
}
.var{
    font-size: 14px;
    font-weight: 300;
    color: #ffffff; /* Ensure high contrast with background */
}
.theLink{
    font-size: 14px;
    font-weight: 300;
    color: #adff2f; /* Ensure high contrast with background */
    margin-top: 5px;
}
